










































































import moment from "moment";
import Vue from "vue";

function parsedDate(displayDateFormat: string | undefined, dateFormatted: string| undefined){
  const seperateChar = displayDateFormat?.includes("/") ? "/" : displayDateFormat?.includes(".") ? "." : displayDateFormat?.includes("-") ? "-" : "/";
      const splittedFormat = displayDateFormat?.split(seperateChar);
      const yearFormatIndex = splittedFormat?.findIndex(item => item == "YYYY");
      const dateIndex = splittedFormat?.findIndex(item => item == "DD");
      let dateIndexObj = {};
      switch (yearFormatIndex) {
        case 0:
          dateIndexObj = { year: () => dateFormatted?.slice(0, 4) };
          if (dateIndex == 1) {
            dateIndexObj["date"] = () => dateFormatted?.slice(5, 7);
            dateIndexObj["month"] = () => dateFormatted?.slice(8, 10)
          } else {
            dateIndexObj["month"] = () => dateFormatted?.slice(5, 7);
            dateIndexObj["date"] = () => dateFormatted?.slice(8, 10)
          }
          break;
        case 1:
          dateIndexObj = { year: () => dateFormatted?.slice(3, 7) };
          if (dateIndex == 0) {
            dateIndexObj["date"] = () => dateFormatted?.slice(0, 2);
            dateIndexObj["month"] = () => dateFormatted?.slice(8, 10)
          } else {
            dateIndexObj["month"] = () => dateFormatted?.slice(0, 2);
            dateIndexObj["date"] = () => dateFormatted?.slice(8, 10)
          }
          break;
        case 2:
          dateIndexObj = { year: () => dateFormatted?.slice(6, 10) }
          if (dateIndex == 0) {
            dateIndexObj["date"] = () => dateFormatted?.slice(0, 2);
            dateIndexObj["month"] = () => dateFormatted?.slice(3, 5)
          } else {
            dateIndexObj["month"] = () => dateFormatted?.slice(0, 2);
            dateIndexObj["date"] = () => dateFormatted?.slice(3, 5)
          }
          break;
        default:
          break;
      }
      let year = Number(dateIndexObj["year"]());
      let month = Number(dateIndexObj["month"]());
      let date = Number(dateIndexObj["date"]());
      return { seperateChar, splittedFormat, year, month, date }
}

export default Vue.extend({
  name: "VInlineDatepicker",
  data() { return {
    displayDateFormat: undefined as string | undefined,
    serviceDateFormat: undefined as string | undefined,
    vuetifyDateFormat: "YYYY-MM-DD",
    valueInternal: new Date().toISOString().substr(0, 10) as any,
    internalValue: null as any,
    dateFormatted: "",
    menu1: false,
    idTest: null as string | null,
    isDateChangedBefore: false,
    monthFormat: "YYYY-MM",
    isClickClear:false,
  }},
  props: {
    openOnlyIconClick: {
      type: Boolean,
    },
    allowedDates: {
      type: Function,
    },
    clearable: {
      type: Boolean,
      default: true
    },  
     weekdayFormat: {
      type: Function,
    },
    placeHolder: {
      type: String,
    },
    firstDayOfWeek: {
      type: [String, Number],
    },
    value: {
      type: [Date, String],
    },
    hideDetails: {
      type: [String, Boolean],
      default: false,
    },
    hideMenu: {
      type: Boolean,
      default: false
    },
    hint: {
      type: String,
      default: "",
    },
    outlined: {
      type: Boolean,
      default: false,
    },
    type: {
      type: String,
    },
    willAttach: {
      type: Boolean,
      default: true,
    },
    persistentHint: {
      type: Boolean,
    },
    persistentPlaceholder: {
      type: Boolean,
    },
    readonlyDatepicker: {
      type: Boolean,
    },
    readonlyTextField: {
      type: Boolean,
    },
    rounded:{
      type: Boolean,
      default: false,
    },
    multiple: {
      type: Boolean,
      default: false,
    },
    range: {
      type: Boolean,
      default: false,
    },
    disabled: {
      type: Boolean,
      default: false,
    },
    min: {
      type: String,
      default: "",
    },
    max: {
      type: String,
      default: "",
    },
    label: {
      type: String,
      default: "Pick a date",
    },
    _renderingProps: {
      type: Object,
      default: null,
    },
    rules: {
      type: Array,
    },
    dateNow: {
      type: Boolean,
      default: false,
    },
    locale: {
      type: String,
      default: "tr",
    },
    dense: {
      type: Boolean,
      default: false,
    },
    filled: {
      type: Boolean,
      default: false,
    },
    textFieldClass: {
      type: String,
      default: "",
    },
    separateChar: {
      type: String,
      default: ",",
    },
    height: {
      type: [String, Number],
    },
    tabindex: {
      type: [String, Number]
    },
    solo:{
      type: Boolean,
      default: false,
    },
    soloInverted:{
      type: Boolean,
      default: false,
    },
    prependInnerIcon: {
      type: String,
      default: "mdi-calendar",
    },
  },
  computed: {
    parsedDate() {
      const retVal = parsedDate(this.displayDateFormat, this.dateFormatted);
      return retVal;
    }
  },
  created() {
    this.idTest = this.CreateGuid();
    if (this.value) {
      this.internalValue = this.value;
    }
  },

  mounted() {
    (this.$refs.text as any).visible = true;
    this.displayDateFormat = this.getDisplayFormat();
    this.serviceDateFormat = this.getServiceFormat();
    this.initialize();
    if (this.disabled == true) {
      // this.label = "";
    }

    Object.defineProperty(this, "value", {
      get: () => this.internalValue,
      set: (value) => (this.internalValue = value),
    });
  },
  destroyed() {
      this.$el?.remove();
  },
  watch: {
    // datepicker componentinin current region a göre güncellemesi icin kullanılır
    // '$engine.region.currentRegion': function (newVal, oldVa) {
    //   this.displayDateFormat = this.$engine.shellConfigs.getFormattingDefinitionByCurrentRegion().displayDateFormat.value.toUpperCase();
    //   if(this.dateFormatted && this.dateFormatted != "") this.DateChange();
    // },
    dateNow: function () {
      if (this.dateNow) {
        this.dateFormatted = moment().format(this.displayDateFormat);
        this.internalValue = moment().format(this.serviceDateFormat);
      }
    },
    max: function () {
      if (this.max) {
        (this.max as any) = moment(this.max, this.serviceDateFormat).format(
          this.vuetifyDateFormat
        );
      }
    },
    min: function () {
      if (this.min) {
        (this.min as any) = moment(this.min, this.serviceDateFormat).format(
          this.vuetifyDateFormat
        );
      }
      if (
        moment(this.min, this.vuetifyDateFormat).format(
          this.serviceDateFormat
        ) > this.internalValue
      ) {
        this.internalValue = [];
      }
    },

    disabled: function () {
      if (this.disabled == true) {
        // this.label = "";
      }
    },
    internalValue: function (newValue, oldValue) {
      this.$emit("change");
      if (typeof newValue == "object") {
        if (!this.arraysEqual(newValue, oldValue)) {
          this.valueInternal = [];
          for (let index = 0; index < newValue.length; index++) {
            let tempValue = moment(
              newValue[index],
              this.serviceDateFormat
            ).format(this.vuetifyDateFormat);
            this.valueInternal[index] = tempValue;
          }
        }
      } else {
        this.dateFormatted = (this as any).qValue;
        if (!(newValue === oldValue)) {
          if (!newValue) {
            return;
          }
          let tempValue = moment(
            this.internalValue,
            this.serviceDateFormat
          ).format(this.vuetifyDateFormat);

          if (tempValue == this.valueInternal) {
            if (typeof newValue == "object") {
              if (!this.arraysEqual(newValue, oldValue)) {
                this.internalValue = [];
                for (let index = 0; index < newValue.length; index++) {
                  let tempValue = moment(
                    newValue[index],
                    this.vuetifyDateFormat
                  ).format(
                    this.serviceDateFormat //sagdaki service
                  );
                  this.internalValue[index] = tempValue;
                }
                this.DateChange();
              }
            } else {
              if (!(newValue === oldValue)) {
                let tempValue = moment(
                  this.valueInternal,
                  this.vuetifyDateFormat
                ).format(
                  this.serviceDateFormat //sagdaki service
                );
                this.internalValue = tempValue;
                this.DateChange();
              }
            }
          }

          this.valueInternal = tempValue;
        }
      }
    },
    valueInternal: function (newValue, oldValue) {
      if(newValue != null && this.dateFormatted != null){
        this.changeValueInternal(newValue, oldValue);
      }
    },
  },

  methods: {
    //Bunun olmaması gerekiyor bakılacak
    changeMenuValue() {
      if(this.hideMenu==true){
        this.menu1 = false
      }else if(this.openOnlyIconClick == true){
        this.menu1 = true
      }else {
        this.menu1 = true
      }
    },
    onKeyUp(event) {
      if (!this.multiple) {
        let targetValue = event.target.value;
        const firstRegex = new RegExp("^\\d{" + this.parsedDate.splittedFormat?.[0].length + "}$", "g");
        const secondRegex = new RegExp("^\\d{" + this.parsedDate.splittedFormat?.[0].length + '}' + this.parsedDate.seperateChar + "\\d{" + this.parsedDate.splittedFormat?.[1].length + "}$", "g");
        if (targetValue.match(firstRegex) !== null || targetValue.match(secondRegex) !== null) {
          event.target.value += this.parsedDate.seperateChar;
        }
      }
    },
    onBlur(event) {
      this.$emit("blur", event.target.value);
    },
    onKeyDown(event) {
      if (
        !this.multiple &&
        !event.key.match(/^[0-9]+$/) &&
        event.key != "Backspace" &&
        event.key != "Tab"
      ) {
        event.preventDefault();
      }
    },
    initialize() {
      if (this.dateNow) {
        this.dateFormatted = moment().format(this.displayDateFormat);
        this.internalValue = moment().format(this.serviceDateFormat);
      }
      if (this.max) {
        (this.max as any) = moment(this.max, this.serviceDateFormat).format(
          this.vuetifyDateFormat
        );
      }
      if (this.min) {
        (this.min as any) = moment(this.min, this.serviceDateFormat).format(
          this.vuetifyDateFormat
        );
      }
      if (this.type == "month") {
        moment(this.dateFormatted, this.vuetifyDateFormat).format(
          this.monthFormat
        );
      }
    },
    changeValueInternal(newValue, oldValue) {
      this.isDateChangedBefore = true;
      if (typeof newValue == "object") {
        if (!this.arraysEqual(newValue, oldValue)) {
          this.internalValue = null;
          if (newValue.length > 0) {
            this.internalValue = [newValue.length];
          }
          for (let index = 0; index < newValue.length; index++) {
            let tempValue = moment(
              newValue[index],
              this.vuetifyDateFormat
            ).format(
              this.serviceDateFormat //sagdaki service
            );
            this.internalValue[index] = tempValue;
          }
          this.DateChange();
        }
      } else {
        if (!(newValue === oldValue)) {
          let tempValue = moment(
            this.valueInternal,
            this.vuetifyDateFormat
          ).format(
            this.serviceDateFormat //sagdaki service
          );
          this.internalValue = tempValue;
          this.DateChange();
        }
      }
    },
    getDisplayFormat() {
      // ? for null has error for websdk prod build.
      let dory = this._renderingProps.context().GetItemSingle("Dory");
      let currentRegion =
        dory.shellConfigs.getFormattingDefinitionByCurrentRegion();
      if (currentRegion) {
        return currentRegion.displayDateFormat.value.toUpperCase();
      }
      return "DD/MM/YYYY";
    },
    getServiceFormat() {
      // ? for null has error for websdk prod build.
      let dory = this._renderingProps.context().GetItemSingle("Dory");
      let currentRegion =
        dory.shellConfigs.getFormattingDefinitionByCurrentRegion();
      if (currentRegion) {
        return currentRegion.serviceDateFormat.value.toUpperCase();
      }
      return "YYYY-MM-DD";
    },
    CreateGuid() {
      var lut = [];
      for (var i = 0; i < 256; i++) {
        (lut[i] as any) = (i < 16 ? "0" : "") + i.toString(16);
      }

      var d0 = (Math.random() * 0xffffffff) | 0;
      var d1 = (Math.random() * 0xffffffff) | 0;
      var d2 = (Math.random() * 0xffffffff) | 0;
      var d3 = (Math.random() * 0xffffffff) | 0;
      return (
        lut[d0 & 0xff] +
        lut[(d0 >> 8) & 0xff] +
        lut[(d0 >> 16) & 0xff] +
        lut[(d0 >> 24) & 0xff] +
        "-" +
        lut[d1 & 0xff] +
        lut[(d1 >> 8) & 0xff] +
        "-" +
        lut[((d1 >> 16) & 0x0f) | 0x40] +
        lut[(d1 >> 24) & 0xff] +
        "-" +
        lut[(d2 & 0x3f) | 0x80] +
        lut[(d2 >> 8) & 0xff] +
        "-" +
        lut[(d2 >> 16) & 0xff] +
        lut[(d2 >> 24) & 0xff] +
        lut[d3 & 0xff] +
        lut[(d3 >> 8) & 0xff] +
        lut[(d3 >> 16) & 0xff] +
        lut[(d3 >> 24) & 0xff]
      );
    },

    //Manuel date girişi
    textinput() {
      if(!this.dateFormatted) {  
        if(this.isClickClear) {
          this.isClickClear=false;
          return;
        }
        this.resetValue();
        return; 
      }
      if(this.dateFormatted?.length == 0) {
        this.resetValue();
        return; 
      }
      if (this.dateFormatted.length == 10 && moment(this.dateFormatted, this.displayDateFormat).isValid()) {
        this.internalValue = moment(this.dateFormatted, this.displayDateFormat).format(this.serviceDateFormat);
      }
      if (
        this.min &&
        this.dateFormatted.length == 10 &&
        moment(this.min, this.vuetifyDateFormat).format(
          this.displayDateFormat
        ) > this.dateFormatted
      ) {
        this.internalValue = ""; 
      }
      if ((this.dateFormatted.length == 2 && this.parsedDate.date < 1 ||
        this.dateFormatted.length == 5 && this.parsedDate.month < 1) ||
        this.dateFormatted.length == 10 && 
        ( this.parsedDate.year < 1 || moment(this.dateFormatted, this.displayDateFormat) > moment(this.max, this.vuetifyDateFormat) ||
        moment(this.dateFormatted, this.displayDateFormat) < moment(this.min, this.vuetifyDateFormat))) {
          this.resetValue();
          return;
      }
      let lastDay = new Date(this.parsedDate.year, this.parsedDate.month, 0).getDate();
      if (this.parsedDate.date > lastDay || this.parsedDate.month > 12 || 
        isNaN(this.parsedDate.date) || isNaN(this.parsedDate.month) || isNaN(this.parsedDate.year)) {
        this.resetValue();
      }
     
    },

    resetValue() {
      this.isDateChangedBefore = false;
        this.dateFormatted = "";
      if (!this.isClickClear){
          (this.$refs.text as any).reset();
      }
        this.internalValue = null;
        (this as any).qValue = "";
        this.valueInternal = null;
      this.$emit("onClear");
    },

    reset() {
      this.dateFormatted = "";
      (this.$refs.text as any).reset();
      this.internalValue = null;
      (this as any).qValue = "";
      this.valueInternal = null;
    },

    resetValidation() {
      (this.$refs.text as any).resetValidation();
    },
    validationRefs() {
      return [this.$refs.text];
    },
    arraysEqual(a, b) {
      if (a === b) return true;
      if (a == null || b == null) return false;
      if (a.length !== b.length) return false;
      for (var i = 0; i < a.length; ++i) {
        if (a[i] !== b[i]) return false;
      }
      return true;
    },

    DateChange() {
      if(!this.range || (this.range && this.valueInternal.length === 2)) {
        this.menu1 = false;
      }

      if (
        !this.isDateChangedBefore &&
        this.valueInternal == new Date().toISOString().substr(0, 10)
      ) {
        this.changeValueInternal(this.valueInternal, "");
      }

      if (typeof this.valueInternal == "object") {
        this.dateFormatted = "";
        this.valueInternal.forEach((element, index) => {
          // invalid date kontrolu yap degılse ekle
          if (
            moment(element, this.vuetifyDateFormat).format(
              this.displayDateFormat
            ) != "Invalid Date"
          ) {
            let prependString =
              index == this.valueInternal.length - 1
                ? ""
                : `${this.separateChar} `;
            this.dateFormatted +=
              moment(element, this.vuetifyDateFormat).format(
                this.displayDateFormat
              ) + prependString; //saydaki display
          }
        });
      } else {
        if (this.type == "month") {
          this.displayDateFormat = this.monthFormat;
          this.serviceDateFormat = this.monthFormat;
          this.vuetifyDateFormat = this.monthFormat;
          this.valueInternal == new Date().toISOString().substr(0, 7);
        }

        this.dateFormatted = moment(
          this.valueInternal,
          this.vuetifyDateFormat
        ).format(this.displayDateFormat); //sagdaki dsipaly
      }

      //this.$emit("change");
    },
  },
});
